.wrapper {
  overflow: hidden;
}
.btn-white {
  background: #fff;
  border: 1px solid #55C16D;
  color: #55C16D;
  &:hover {
    background: #55C16D;
    border-color: #fff;
    color: #fff;
  }
}
.btn-green {
  background: #5CC280;
  border: 1px solid #5CC280;
  color: #fff;
  &:hover {
    background: #fff;
    color: #5CC280;
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.btn-gray {
  background: #EAEAEA;
  border: 1px solid #E2E2E2;
  color: #848484;
  &:hover {
    background: #E2E2E2;
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.h2 {
  font-size: 40px;
  line-height: 120%;
}
.section-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  &__btn {
    height: 45px;
    flex-shrink: 0;
    background: #fff;
    border-radius: 24px;
    padding: 5px 25px;
    &:hover {
      background: #55C16D;
      color: #fff;
    }
    span {
      font-size: 16px;
      line-height: 120%;
    }
  }
}
.ymaps-2-1-79-balloon {
  background: transparent !important;
  box-shadow: unset !important;
}
.ymaps-2-1-79-balloon__layout {
  background: transparent !important;
  ymaps {
    height: auto !important;
  }
}
.ymaps-2-1-79-balloon__content {
  background: transparent !important;
  border-radius: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
}
.ymaps-2-1-79-balloon__close {
  display: none;
}
.balloon {
  min-width: 153px;
  display: flex;
  align-items: center;
  gap: 8px;
  background: #55C16D;
  border-radius: 300px;
  padding: 5px 7px;
  font-family: "TildaSans";
  color: #fff;
  &__icon {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #92E49A;
    border-radius: 50%;
    img {
      width: 24px;
    }
  }
  &__title {
    font-size: 14px;
    line-height: 130%;
    font-weight: 800;
  }
  &__text {
    font-size: 10px;
    line-height: 130%;
    font-weight: 500;
  }
}
.viber {
  background: #7F4DA0;
}
.telegram {
  background: linear-gradient(203.32deg, rgb(55, 174, 226) 21.452%,rgb(30, 150, 200) 70.215%);
}
.whatsapp {
  background: #25D366;
}
.viber,
.telegram,
.whatsapp {
  &:hover {
    opacity: 0.7;
  }
}
.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px;
  }
  &__item {
    position: relative;
    font-size: 14px;
    line-height: 120%;
    color: rgba(35, 35, 35, 0.55);
    &--active {
      color: #232323;
    }
    &:hover {
      color: #232323;
    }
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -10px;
        transform: translate(0, -50%);
        width: 4px;
        height: 1px;
        background: #232323;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 32px;
  }
  .section-top {
    &__btn {
      height: 38px;
      padding: 5px 20px;
      span {
        font-size: 14px;
      }
    }
  }
}