.catalog {
  padding: 40px 0 50px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  &__sidebar {
    width: 450px;
    min-height: 280px;
    background: #fff;
    border-radius: 10px;
    padding: 25px;
    &-link {
      display: flex;
      align-items: center;
      gap: 12px;
      margin-bottom: 25px;
      &:hover {
        color: #5CC280;
        .catalog__sidebar-arrow {
          svg path {
            stroke: #5CC280;
          }
        }
        .catalog__sidebar-icon {
          svg rect {
            fill: #5CC280;
          }
        }
      }
    }
    &-arrow {
      width: 7px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &-icon {
      width: 16px;
      svg {
        width: 100%;
        height: auto;
      }
      svg rect {
        transition: all 0.3s;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__content {
    width: calc(100% - 490px);
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &__item {
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
      cursor: pointer;
    }
    .item-show--active {
      .catalog__item-title,
      .catalog__item-btn {
        background: #DEF8E0;
        svg {
          transform: rotate(-180deg);
        }
      }
    }
    &-title {
      height: 40px;
      flex-grow: 1;
      display: flex;
      align-items: center;
      border-radius: 6px;
      padding: 5px 16px;
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
    &-btn {
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 6px;
      svg {
        width: 10px;
        height: auto;
        transition: all 0.3s;
      }
    }
    &-hidden {
      display: none;
      padding-left: 40px;
      margin-top: 14px;
    }
    .item-hidden--active {
      display: block;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 14px;
      li a {
        font-size: 16px;
        line-height: 120%;
      }
      li a:hover {
        color: #5CC280;
      }
    }
  }
  &__title {
    margin-top: 30px;
  }
  &__number-products {
    display: block;
    font-size: 14px;
    line-height: 120%;
    font-weight: 500;
    margin-top: 10px;
  }
  &__list-mob {
    display: none;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
    margin-top: 20px;
  }
}
.catalog-card {
  position: relative;
  &:hover {
    .catalog-card__img {
      img {
        transform: scale(1.125);
      }
    }
  }
  &__img {
    position: relative;
    border-radius: 10px;
    padding-bottom: 82%;
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.3);
      border-radius: 10px;
      pointer-events: none;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      transition: all 0.3s;
    }
  }
  &__title {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 16px;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: #fff;
  }
}

@media screen and (max-width: 1400px) {
  .catalog {
    &__inner {
      gap: 20px;
    }
    &__sidebar {
      width: 380px;
    }
    &__content {
      width: calc(100% - 400px);
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .catalog {
    &__cards2 {
      @media screen and (min-width: 993px) {
        grid-template-columns: repeat(2, calc(50% - 20px / 2));
      }
    }
  }
  .catalog-card {
    &__title {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog {
    padding-top: 10px;
    &__inner {
      flex-direction: column;
    }
    &__sidebar {
      display: none;
    }
    &__content {
      width: 100%;
    }
    &__title {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-end;
      gap: 8px;
      margin-top: 10px;
    }
    &__number-products {
      color: rgba(35, 35, 35, 0.38);
      margin-top: 0;
    }
    &__list-mob {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 10px;
      margin-top: 10px;
      &--active {
        li {
          display: block !important;
        }
      }
      li:nth-child(5) ~ li {
        display: none;
      }
      .catalog__list-btn {
        height: 30px;
        display: flex !important;
        justify-content: center;
        align-items: center;
        background: transparent;
        border: 1px solid #DDDDDD;
        border-radius: 40px;
        padding: 5px 15px;
        font-size: 14px;
        line-height: 120%;
        font-weight: 500;
      }
      li a {
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        border-radius: 40px;
        padding: 5px 15px;
        font-size: 14px;
        line-height: 120%;
        font-weight: 500;
      }
    }
    &__cards {
      margin-top: 10px;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    &__cards2 {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    padding-bottom: 30px;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
      gap: 10px;
    }
    &__cards2 {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
    .product-card {
      padding: 10px;
      &__favorite {
        top: 10px;
        right: 10px;
        width: 18px;
        height: 18px;
        svg {
          width: 8px;
          height: auto;
        }
      }
      &__desc {
        margin-top: 10px;
      }
      &__group {
        margin-top: 5px;
      }
      &__price {
        font-size: 12px;
      }
      &__weight {
        font-size: 11px;
      }
      &__btn {
        height: 26px;
        border-radius: 5px;
        margin-top: 10px;
        span {
          font-size: 10px;
        }
      }
    }
  }
  .catalog-card {
    &__title {
      padding: 8px;
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 359px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
}