.header {
  &__logo-title {
    font-size: 22px;
    line-height: 120%;
    font-weight: 800;
    text-transform: uppercase;
  }
  &__logo-subtitle {
    font-size: 16px;
    line-height: 120%;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 40px;
    li a {
      font-size: 14px;
      line-height: 120%;
      &:hover {
        color: #5CC280;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__social {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    img {
      max-width: 15px;
    }
  }
  &__phone {
    font-size: 14px;
    line-height: 120%;
    font-weight: 600;
  }
  &__btn {
    gap: 8px;
    background: #F1F1F1;
    border-radius: 135px;
    padding: 5px 10px 5px 5px;
    &:hover {
      background: #E2E2E2;
    }
    i {
      width: 27px;
      height: 27px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #5CC280;
      border-radius: 50%;
      img {
        max-width: 80%;
        max-height: 80%;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
    }
  }
  &__catalog-show {
    height: 47px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    background: #5CC280;
    border-radius: 10px;
    padding: 5px 20px;
    cursor: pointer;
    transition: all 0.3s;
    i {
      width: 17px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      color: #fff;
    }
  }
  &__search {
    position: relative;
    flex-grow: 1;
    input {
      width: 100%;
      height: 47px;
      background: #fff;
      border: 1px solid transparent;
      border-radius: 7px;
      padding: 0 15px 0 38px;
      font-size: 14px;
      line-height: 120%;
      font-weight: 600;
      &::placeholder {
        color: rgba(0, 0, 0, 0.45);
      }
    }
    span {
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 15px;
      transform: translate(0, -50%);
      width: 15px;
      img {
        width: 100%;
      }
    }
  }
  &__links {
    display: flex;
    align-items: center;
    gap: 10px;
    .header__catalog {
      display: none;
    }
  }
  &__link {
    height: 47px;
    border-radius: 10px;
    padding: 5px 19px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
  &__link2 {
    background: #fff;
    color: #232323;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__basket-show,
  &__user {
    width: 47px;
    height: 47px;
    background: #fff;
    border-radius: 10px;
    svg {
      max-width: 70%;
      max-height: 70%;
    }
  }
}
.header-top {
  background: #fff;
  border-radius: 0 0 15px 15px;
  padding: 10px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 60px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  .header__group {
    display: none;
  }
}
.header-bottom {
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__left {
    flex-grow: 1;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__right {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .header__list {
    display: none;
  }
  .header__socials,
  .header__phone,
  .header__btn {
    display: none;
  }
}
.burger {
  position: relative;
  width: 33px;
  height: 28px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    height: 2px;
    background: #55C16D;
    border-radius: 14px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
    left: 0;
    width: 100%;
  }
  span:nth-child(2) {
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: 24px;
  }
  span:nth-child(3) {
    left: 0;
    bottom: 0;
    width: 100%;
  }
  &--active {
    span:nth-child(1) {
      top: 13px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 13px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1500px) {
  .header {
    &__list {
      gap: 30px;
    }
  }
  .header-top {
    &__inner {
      gap: 30px;
    }
    &__left {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1350px) {
  .header {
    &__list {
      gap: 24px;
    }
  }
  .header-top {
    &__inner {
      gap: 24px;
    }
    &__left {
      gap: 24px;
    }
    &__right {
      gap: 24px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__logo-title {
      font-size: 13px;
    }
    &__logo-subtitle {
      font-size: 11px;
    }
    &__catalog-show {
      height: 36px;
      padding: 5px 12px;
      span {
        font-size: 14px;
      }
    }
    &__search {
      input {
        height: 36px;
      }
    }
    &__link {
      height: 36px;
      padding: 5px 15px;
      font-size: 14px;
    }
    &__basket-show,
    &__user {
      width: 36px;
      height: 36px;
      svg {
        max-width: 14px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__list {
      li a {
        font-size: 12px;
      }
    }
    &__social {
      width: 20px;
      height: 20px;
      img {
        max-width: 12px;
      }
    }
    &__phone {
      font-size: 12px;
    }
    &__btn {
      gap: 5px;
      i {
        width: 20px;
        height: 20px;
      }
      span {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__list {
      gap: 20px;
    }
  }
  .header-top {
    &__inner {
      gap: 20px;
    }
    &__left {
      gap: 20px;
    }
    &__right {
      gap: 20px;
    }
  }
}

@media screen and (max-width: 1050px) {
  .header {
    &__list {
      gap: 14px;
    }
  }
  .header-top {
    &__inner {
      gap: 14px;
    }
    &__left {
      gap: 14px;
    }
    &__right {
      gap: 14px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    position: relative;
    z-index: 5;
    padding-bottom: 18px;
    &__list {
      display: none;
      li a {
        font-size: 14px;
      }
    }
    &__social {
      width: 42px;
      height: 42px;
      img {
        width: 100%;
        max-width: 25px;
      }
    }
    &__phone {
      font-size: 22px;
    }
    &__btn {
      gap: 8px;
      i {
        width: 27px;
        height: 27px;
      }
      span {
        font-size: 14px;
      }
    }
    &__catalog-show {
      span {
        font-size: 16px;
      }
    }
    &__search {
      width: 100%;
      input {
        height: 40px;
        background: #F6F6F6;
      }
    }
    &__links {
      flex-wrap: wrap;
      .header__catalog {
        display: block;
      }
    }
    &__link {
      padding: 5px 20px;
      font-size: 16px;
    }
    .remove-mob {
      display: none;
    }
    &__basket-show,
    &__user {
      background: #F6F6F6;
    }
  }
  .header-top {
    position: relative;
    z-index: 3;
    .header__socials,
    .header__phone,
    .header__btn {
      display: none;
    }
    .header__group {
      display: flex;
    }
  }
  .header-bottom {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    padding: 50px 0 25px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
    &__inner {
      flex-direction: column;
      justify-content: start;
      align-items: start;
      gap: 30px;
    }
    &__left {
      width: 100%;
      flex-direction: column;
      align-items: start;
      gap: 15px;
      .header__catalog-show {
        display: none;
      }
    }
    &__right {
      flex-direction: column;
      align-items: start;
      gap: 25px;
    }
    .header__list {
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 13px;
    }
    .header__group {
      display: none;
    }
    .header__socials,
    .header__phone,
    .header__btn {
      display: flex;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__logo-title {
      font-size: 12px;
    }
    &__logo-subtitle {
      font-size: 10px;
    }
  }
  .header-top {
    &__inner {
      gap: 10px;
    }
    &__right {
      gap: 10px;
    }
  }
}
