.main {
  padding-bottom: 35px;
  &__inner {
    position: relative;
    background: #55C16D;
    border-radius: 15px;
    padding: 150px 32px 32px 32px;
    overflow: hidden;
  }
  &__group {
    position: relative;
    z-index: 3;
    max-width: 800px;
    margin-left: 16px;
  }
  &__title {
    font-size: 81px;
    line-height: 120%;
    font-weight: 800;
    color: #fff;
  }
  &__text {
    position: absolute;
    left: calc(100% - 130px);
    bottom: 15px;
    max-width: 300px;
    width: 100%;
    font-size: 24px;
    line-height: 120%;
    color: #fff;
  }
  &__hashtags {
    position: absolute;
    top: -45px;
    left: 10px;
    .main__hashtag:nth-child(1) {
      transform: rotate(-16deg);
    }
    .main__hashtag:nth-child(2) {
      position: absolute;
      top: 32px;
      left: calc(100% - 32px);
      transform: rotate(5deg);
    }
  }
  &__hashtags2 {
    position: absolute;
    top: 70px;
    left: calc(100% - 150px);
    .main__hashtag:nth-child(1) {
      transform: rotate(-24deg);
    }
    .main__hashtag:nth-child(2) {
      position: absolute;
      top: 40px;
      left: calc(100% - 64px);
      transform: rotate(8deg);
    }
  }
  &__hashtag {
    min-width: 149px;
    height: 44px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 130px;
    padding: 5px 10px;
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
  &__btn {
    position: relative;
    z-index: 3;
    width: 100%;
    height: 90px;
    border-radius: 48px;
    padding: 15px;
    margin-top: 40px;
    span {
      font-size: 24px;
      font-weight: 600;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    max-width: 568px;
    max-height: 100%;
    height: 100%;
    pointer-events: none;
    img {
      max-width: 100%;
      max-height: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__img2 {
    position: absolute;
    top: 0;
    left: -100px;
    pointer-events: none;
    img:nth-child(2) {
      display: none;
    }
  }
}
.products {
  padding: 50px 0;
  &2 {
    padding-top: 35px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 120px / 4));
    gap: 40px;
    margin-top: 30px;
  }
}
.product-card {
  display: flex;
  flex-direction: column;
  background: #fff;
  box-shadow: 0px 4px 25.3px 0px rgba(0, 0, 0, 0.03);
  border-radius: 11px;
  padding: 23px;
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 77%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.125);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 9px;
      transition: all 0.3s;
    }
  }
  &__favorite {
    position: absolute;
    z-index: 2;
    top: 16px;
    right: 16px;
    width: 41px;
    height: 41px;
    background: #fff;
    border-radius: 50%;
    padding: 0;
    svg {
      max-width: 80%;
      max-height: 80%;
    }
  }
  .favorite--active {
    svg path {
      fill: #FE99C5;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 15px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.24);
  }
  &__title {
    display: block;
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: auto;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    margin-top: 32px;
  }
  &__price {
    font-size: 22px;
    line-height: 120%;
    font-weight: 700;
  }
  &__weight {
    font-size: 18px;
    line-height: 120%;
    color: #AFAFAF;
  }
  &__btn {
    width: 100%;
    height: 65px;
    border-radius: 12px;
    padding: 5px 10px;
    margin-top: 15px;
    span {
      font-size: 22px;
    }
  }
}
.video-section {
  padding: 50px 0;
  .video__wrap {
    position: relative;
    display: block;
    margin-top: 30px;
  }
  .video__img {
    background: #fff;
    box-shadow: 0px 4px 25.3px 0px rgba(0, 0, 0, 0.03);
    border-radius: 10px;
    padding: 24px;
    img {
      width: 100%;
      min-height: 292px;
      border-radius: 5px;
    }
  }
  .video__play {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 230px;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    padding: 24px 24px 24px 78px;
    cursor: pointer;
  }
  .video__play-icon {
    position: absolute;
    top: 50%;
    left: -68px;
    transform: translate(0, -50%);
    width: 122px;
    height: 122px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(165, 136, 120, 0.6);
    backdrop-filter: blur(14.3px);
    border-radius: 50%;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 94px;
      height: 94px;
      background: #A58878;
      border-radius: 50%;
    }
    img {
      position: relative;
      z-index: 2;
      width: 16px;
    }
  }
  .video__title {
    font-size: 16px;
    line-height: 120%;
    text-decoration: underline;
    color: #646E79;
  }
  .video__duration {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-top: 9px;
    i {
      width: 15px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 130%;
      color: #646E79;
    }
  }
}
.suppliers {
  padding: 50px 0;
  &__slider {
    position: relative;
    margin-top: 40px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    width: 252.47px !important;
    height: auto;
  }
}
.supplier-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #fff;
  box-shadow: 0px 0px 23.2px 0px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 32px 16px;
  &__img {
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.06);
    border-radius: 9px;
    img {
      max-width: 90%;
      max-height: 90%;
    }
  }
  &__desc {
    margin-top: 18px;
  }
  &__name {
    font-size: 22px;
    line-height: 120%;
    font-weight: 500;
  }
  &__number {
    font-size: 14px;
    line-height: 120%;
    font-weight: 500;
    color: #5CC280;
    margin-top: 5px;
  }
}
.feedback {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 70px;
    background: #fff;
    box-shadow: 0px 4px 25.3px 0px rgba(0, 0, 0, 0.03);
    border-radius: 16px;
    padding: 40px;
  }
  &__group {
    width: 325px;
    display: flex;
    flex-direction: column;
    align-items: start;
  }
  &__title {
    color: #594B44;
  }
  &__btn {
    height: 56px;
    display: inline-flex;
    border-radius: 37px;
    padding: 5px 27px;
    margin-top: 20px;
    margin-bottom: auto;
    span {
      font-size: 16px;
    }
  }
  &__btn2 {
    width: 100%;
    height: 83px;
    border-radius: 48px;
    padding: 10px 27px;
    margin-top: 20px;
    span {
      font-size: 24px;
    }
  }
  &__items {
    width: calc(100% - 395px);
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__item {
    max-width: 328px;
    width: 100%;
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 600;
      margin-bottom: 15px;
    }
    &-img {
      display: block;
      img {
        max-width: 100%;
        border-radius: 10px;
      }
    }
  }
}
.map-section {
  padding: 50px 0 100px;
  .map__wrap {
    position: relative;
    min-height: 366px;
    padding-bottom: 35%;
    margin-top: 30px;
  }
  .map{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    .ymaps-2-1-79-map ymaps {
      border-radius: 12px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .main {
    &__img {
      right: -100px;
    }
  }
}

@media screen and (max-width: 1500px) {
  .main {
    &__group {
      max-width: 700px;
    }
    &__title {
      font-size: 70px;
    }
    &__text {
      bottom: 10px;
    }
    &__hashtags {
      top: -50px;
    }
    &__hashtags2 {
      top: 50px;
      left: calc(100% - 100px);
    }
    &__img2 {
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    &__group {
      max-width: 600px;
    }
    &__title {
      font-size: 60px;
    }
    &__text {
      font-size: 20px;
    }
    &__hashtags {
      top: -60px;
    }
    &__hashtags2 {
      top: 30px;
    }
    &__btn {
      height: 80px;
      span {
        font-size: 20px;
      }
    }
  }
  .products {
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
    }
  }
  .feedback {
    &__inner {
      gap: 30px;
    }
    &__group {
      width: 300px;
    }
    &__items {
      width: calc(100% - 330px);
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__group {
      max-width: 500px;
      margin-left: 0;
    }
    &__title {
      font-size: 48px;
    }
    &__text {
      position: static;
      max-width: 100%;
      font-size: 18px;
      margin-top: 8px;
    }
    &__hashtags2 {
      top: 10px;
      left: calc(100% - 80px);
    }
    &__btn {
      height: 68px;
      margin-top: 30px;
      span {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__img2 {
      top: unset;
      bottom: 0;
    }
  }
  .products {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      margin-top: 20px;
    }
  }
  .product-card {
    &__subtitle {
      font-size: 14px;
    }
    &__title {
      font-size: 16px;
    }
    &__group {
      margin-top: 20px;
    }
    &__price {
      font-size: 20px;
    }
    &__weight {
      font-size: 16px;
    }
    &__btn {
      height: 55px;
      span {
        font-size: 18px;
      }
    }
  }
  .video-section {
    .video__wrap {
      margin-top: 20px;
    }
    .video__img {
      padding: 12px;
    }
    .video__play {
      max-width: 175px;
      padding: 20px 16px 20px 48px;
    }
    .video__play-icon {
      left: -35px;
      width: 69px;
      height: 69px;
      &:before {
        width: 53px;
        height: 53px;
      }
    }
    .video__title {
      font-size: 14px;
    }
    .video__duration {
      i {
        width: 13px;
      }
      span {
        font-size: 14px;
      }
    }
  }
  .suppliers {
    &__slider {
      margin-top: 20px;
    }
  }
  .feedback {
    &__inner {
      position: relative;
      flex-direction: column;
      padding: 30px 30px 118px 30px;
    }
    &__group {
      width: 100%;
    }
    &__items {
      width: 100%;
    }
    &__btn {
      height: 37px;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      position: absolute;
      left: 50%;
      bottom: 30px;
      transform: translate(-50%, 0);
      max-width: 312px;
      width: calc(100% - 30px);
      height: 64px;
      margin-top: 0;
      span {
        font-size: 18px;
      }
    }
  }
  .map-section {
    .map__wrap {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    .container {
      padding: 0;
    }
    &__inner {
      padding: 360px 15px 24px 15px;
    }
    &__group {
      max-width: 100%;
    }
    &__hashtags {
      top: -80px;
    }
    &__hashtags2 {
      top: -80px;
      left: unset;
      right: 80px;
    }
    &__img {
      right: 0;
      max-width: 100%;
      width: 100%;
      height: auto;
      img {
        width: 100%;
      }
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__img2 {
      left: -80px;
      bottom: -80px;
      min-width: 680px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  .products {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .feedback {
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
    &__item {
      max-width: 100%;
      &-img {
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 20px;
    &__title {
      font-size: 34px;
    }
    &__hashtags {
      top: -150px;
      left: 15px;
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 28px;
      .main__hashtag:nth-child(1) {
        transform: rotate(16deg);
        margin-left: 15px;
      }
      .main__hashtag:nth-child(2) {
        position: static;
        transform: rotate(-16deg);
      }
    }
    &__hashtags2 {
      top: -95px;
      right: 15px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      gap: 5px;
      .main__hashtag:nth-child(1) {
        transform: rotate(-0.3deg);
      }
      .main__hashtag:nth-child(2) {
        position: static;
        transform: rotate(5deg);
        margin-right: 24px;
      }
    }
  }
  .products {
    padding: 30px 0;
    &2 {
      padding-top: 20px;
    }
  }
  .product-card {
    &__favorite {
      width: 35px;
      height: 35px;
    }
    &__subtitle {
      font-size: 12px;
    }
    &__title {
      font-size: 14px;
    }
    &__price {
      font-size: 18px;
    }
    &__weight {
      font-size: 14px;
    }
  }
  .video-section {
    padding: 30px 0;
  }
  .suppliers {
    padding: 30px 0;
  }
  .feedback {
    padding: 30px 0;
  }
  .map-section {
    padding: 30px 0 60px;
  }
}

@media screen and (max-width: 450px) {
  .main {
    &__inner {
      padding-top: 320px;
    }
  }
  .products {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .feedback {
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 360px) {
  .main {
    &__hashtags {
      left: 0;
    }
    &__hashtags2 {
      right: 0;
    }
  }
}