.footer {
  position: relative;
  color: #fff;
  &__inner {
    position: relative;
    background: #294F31;
    border-radius: 20px;
    padding: 60px 40px 30px;
    overflow: hidden;
  }
  &__top {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 90px;
  }
  &__logo-title {
    font-size: 22px;
    line-height: 120%;
    font-weight: 800;
    text-transform: uppercase;
  }
  &__logo-subtitle {
    font-size: 16px;
    line-height: 120%;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 9px;
    margin-top: 15px;
  }
  &__social {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    img {
      max-width: 15px;
    }
  }
  &__phone {
    display: inline-block;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 15px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 35px;
  }
  &__item {
    &-title {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: rgba(255, 255, 255, 0.5);
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      margin-top: 5px;
    }
  }
  &__bottom {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    gap: 35px;
    margin-top: 60px;
  }
  &__items2 {
    flex-grow: 1;
    display: flex;
    align-items: start;
    gap: 80px;
  }
  &__item2 {
    &-nav {
      display: flex;
      align-items: start;
      gap: 80px;
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      margin-bottom: 15px;
    }
    &-list {
      min-width: 100px;
      display: grid;
      grid-template-columns: 100%;
      gap: 7px;
      li a {
        font-size: 14px;
        line-height: 120%;
        font-weight: 500;
        &:hover {
          color: rgba(255, 255, 255, 0.5);
        }
      }
    }
  }
  &__text {
    display: flex;
    gap: 35px;
    p {
      font-size: 12px;
      line-height: 120%;
      font-weight: 500;
    }
    p a {
      text-decoration: underline;
      &:hover {
        text-decoration: unset;
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      display: block;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer {
    &__top {
      gap: 60px;
    }
    &__items2 {
      gap: 40px;
    }
    &__item2 {
      &-nav {
        gap: 40px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__inner {
      padding: 60px 30px 30px;
    }
    &__top {
      gap: 30px;
    }
    &__items2 {
      gap: 30px;
    }
    &__item2 {
      &-title {
        font-size: 22px;
      }
      &-nav {
        gap: 30px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__inner {
      padding: 30px;
    }
    &__top {
      flex-direction: column;
    }
    &__items2 {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__bottom {
      flex-direction: column;
      gap: 12px;
    }
    &__text {
      flex-direction: column;
      gap: 12px;
    }
    &__img {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}